<template>
  <div>
    <div v-if="pageLoading" class="text-center">
      <i class="fa fa-spinner fa-spin fa-2x" />
    </div>
    <div v-else>
      <b-button variant="primary" class="px-16 lable-btnAny">{{
        $t("SUBSIDYGRANTJUDGEMENTRESULT")
      }}</b-button>
      <br /><br />
      <b-row>
        <b-col xl="1"></b-col>
        <b-col class="title-fw title-fs">
          <span
            ><i
              :class="{
                'fa fa-circle-o fa-2x colorIcon':
                  judgementResult.judge_result_kbn == '01',
                'fa fa-caret-up fa-2x colorIcon':
                  judgementResult.judge_result_kbn == '02',
                'fa fa-times fa-2x colorIcon':
                  judgementResult.judge_result_kbn == '03',
              }"
              aria-hidden="true"
            ></i> </span
          ><span class="pl-2">{{ judgementResult.judge_result }}</span>
        </b-col>
      </b-row>
      <br />
      <b-row class="title-fw title-fs">
        <b-col
          id="judgement-result"
          v-for="content in contents"
          :key="content.id"
          cols="12"
          xl="5"
        >
          <template v-if="content.id % 2 != 0">
            <b-list-group class="horizontalAlign">
              <ul>
                <li>{{ content.listContent }}</li>
              </ul>
            </b-list-group>
          </template>
          <template v-else>
            <b-list-group>
              <ul>
                <li>{{ content.listContent }}</li>
              </ul>
            </b-list-group>
          </template>
        </b-col>
      </b-row>
      <br />
      <b-row>
        <b-col cols="12" xl="2"></b-col>
        <b-col cols="12" xl="7">
          <b-button
            @click="goToSubsidyGrantJudgement"
            variant="primary"
            class="lg-btnAny mr-4 mb-2"
            >{{ $t("REJUDGMENTBTN") }}</b-button
          >
          <b-button
            @click="goToSubsidyAppConfirmation"
            variant="primary"
            class="lg-btnAny mb-2"
            >{{ $t("INQUIRY1BTN") }}<br />{{ $t("INQUIRY2BTN") }}</b-button
          >
        </b-col>
      </b-row>
      <br /><br /><br />
    </div>
  </div>
</template>

<script>
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { mapGetters } from "vuex";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "JudgementResult",
  components: {},
  data() {
    return {
      judgementResult: {},
      pageLoading: false,
      contents: [],
    };
  },
  computed: {
    ...mapGetters({
      judgementPoint: "getSessionJudgementPoint",
    }),
  },
  mounted() {
    this.getJudgementResult();
  },

  methods: {
    /**Based on judg point from store , call API and display judgement Result for QA's */
    async getJudgementResult() {
      this.pageLoading = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.SUBSIDYJUDGEMENTRESULTREAD,
        {
          judge_point: this.judgementPoint.judgePoint,
        }
      );
      this.pageLoading = false;
      if (data.success) {
        let responseData = data.data[0];
        this.judgementResult = responseData;
        let content = this.judgementResult.subsidy_contents.split("ãƒ»");
        let contentArray = [];
        content.forEach((item, index) => {
          if (item && item != "") {
            let splittedContent = {
              id: index,
              listContent: item,
            };
            contentArray.push(splittedContent);
          }
        });
        this.contents = contentArray;
      }
    },

    /**Update the result number to store and route to client data subsidy confirmation screen  */
    goToSubsidyAppConfirmation() {
      this.$store.dispatch("updateJudgementResult", {
        result_no: this.judgementResult.result_no,
      });
      this.$router.push({
        name: "subsidy_app_confirmation",
      });
    },
    /**Function to go back for chart screen and starting the QA's again */
    goToSubsidyGrantJudgement() {
      this.$router.push({ name: "subsidy_grant_judgement_index" });
    },
  },
};
</script>

<style>
.lable-btnAny {
  min-width: 10px !important;
  border-radius: 0px;
  cursor: default !important;
}
.colorIcon {
  color: red;
}
.horizontalAlign {
  padding-left: 8rem !important;
}
.lg-btnAny {
  min-width: 210px !important;
  min-height: 60px !important;
}
#judgement-result ul {
  margin-bottom: 0rem;
}

@media (max-width: 1024px) {
  .horizontalAlign {
    padding-left: 0px !important;
  }
}
</style>